// =============================================================================
// Coupon
// =============================================================================

.#{$wpfs} {
  &-coupon {
    position: relative;
    font-size: 16px;
    line-height: 24px;
    padding-left: spacer(3);
    margin-bottom: spacer(1);

    &:last-of-type {
      margin-bottom: 0;
    }

    &:not(#{&}--warning) {
      &:before {
        @include base-icon;
        content: $wpfs-icon-check-circle;
        position: absolute;
        top: 4px;
        left: 0;
        font-size: 16px;
        font-weight: bold;
        color: var(--success-color);
      }
    }

    &--warning {
      &:before {
        background: url(../images/icons/warning.svg);
        background-size: cover;
        position: absolute;
        top: 4px;
        left: 0;
        width: 16px;
        height: 16px;
        content: '';
      }
    }
  }

  &-coupon-summary {
    font-size: 13px;
  }

  &-coupon-summary-note {
    color: var(--form-base-color);
  }
}
