// =============================================================================
// Summary
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-summary {
    &:not(#{&}--collapsible) {
      background: var(--ui-summary-tooltip-bg-color);
      position: relative;
      width: 250px;
      padding: spacer(1.5) spacer(1);
      border: 1px solid var(--ui-summary-tooltip-border-color);
      border-radius: var(--default-border-radius);

      &:after,
      &:before {
        left: 20px;
        border: solid transparent;
        content: " ";
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
      }

      &:after {
        border-width: 6px;
        margin-left: -6px;
      }

      &:before {
        border-width: 7px;
        margin-left: -7px;
      }
    }

    &--collapsible {
      #{$root}-summary-table {
        font-size: 15px;
      }

      #{$root}-summary-table-cell {
        font-size: 15px;
        line-height: spacer(3);
        padding: 0 0 spacer(1) !important;
      }

      #{$root}-summary-table-total {
        #{$root}-summary-table-cell {
          padding: spacer(1) 0 0 !important;

          &:first-of-type {
            font-weight: bold !important;
          }
        }
      }

      #{$root}-summary-description {
        font-size: 13px;
        margin: 0;

        &:first-of-type {
          margin-top: 0;
        }
      }
    }
  }

  &-summary-table {
    width: 100%;
    font-size: 13px;
    table-layout: fixed;
    border-collapse: collapse;
  }

  &-summary-table-row {
    &:last-child {
      #{$root}-summary-table-cell {
        padding-bottom: spacer(1);
      }
    }
  }

  &-summary-table-cell {
    font-size: 13px;
    padding: spacer(0.5);

    &:last-child {
      font-weight: bold;
      text-align: right;
    }
  }

  &-summary-table-total {
    #{$root}-summary-table-cell {
      background: none;
      padding-top: spacer(1);
      font-size: 15px;
      border-top: 1px solid var(--ui-summary-tooltip-border-color) !important;
    }
  }

  &-summary-description {
    color: var(--ui-summary-tooltip-description-font-color);
    font-size: 12px;
    line-height: 16px;
    margin: 0 spacer(0.5) spacer(2);

    &:first-of-type {
      margin-top: spacer(1.5)
    }

    &:last-of-type {
      margin-bottom: 0;
    }
  }
}
